@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Poppins', sans-serif;
}

p{
  font-size: 1.6rem;
}

h1{
  font-size: 6rem;
}

.app{
  width: 100%;
  position: relative;
  background-color: rgba(0,0,0,0.4);
  color: #fff;
}

.app .search{
  text-align: center;
  padding: 1rem;
}

.app input{
  padding: .7rem 1.5rem;
  font-size: 1.2rem;
  border-radius: 25px;
  border: 1px solid rgba(255,255,255, 0.8);
  background: rgba(255,255,255,0.1);
  color: #f8f8f8;
}

::placeholder{
  color: #868383;
}

.app:before{
  content:'';
  background: url('./assets/sunset.jpg') no-repeat center center/cover;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}

.container{
  max-width: 700px;
  height: 100vh;
  margin: auto;
  padding: 0 1rem;
  position: relative;
  top: 10%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.container .top{
  flex-grow: 0.5;
  width: 100%;
  margin: 1rem auto;
}

.app .description{
  position: relative;
  text-transform:capitalize;
  right: -90%;
  transform-origin: 0 0;
  transform: rotate(269deg)
}

.app .bottom{
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  width: 100%;
  margin: 1rem auto;
  padding: 1rem;
  border-radius: 12px;
  background-color: rgba(255,255,255,0.2);
}

.app .bottom small{
  color: #c7b9b9
}

.bold{
  font-weight: 500;
}

/* ========== MEDIA QUERIES (SMALL SERVICES) ========== */
@media screen and (max-width: 600px){
  .app .bottom p{
    font-size: 1rem;
  }
}
